
import { defineComponent } from "vue";
import { fetchData, fetchingOptions } from "@/services/api";
import SearchBar from "@/components/SearchBar.vue";

export default defineComponent({
    name: "ItemsView",
    components: { SearchBar },
    data: () => {
        return {
            items: null,
            isLoading: false,
            pageNumber: 1,
            totalPages: 1,
            limit: 10,
            page: 1,
            searchQuery: "" as string | null,
            dialogSearchVisible: false,
        };
    },

    created() {
        if (!this.routeQuery.page) {
            this.$router.push({
                name: this.routeName,
                query: { ...this.routeQuery, page: 1 },
            });
        }
        this.page = Number(this.routeQuery.page);
        this.getItems();
    },

    methods: {
        async getItems() {
            try {
                this.isLoading = true;
                const data = await fetchData(fetchingOptions[this.routeName], {
                    search: this.routeQuery.search as string,
                    page: Number(this.routeQuery.page),
                });

                this.items = data.results;

                this.totalPages = Math.ceil(data.count / this.limit);
            } catch (error) {
                Promise.reject(error);
            } finally {
                this.isLoading = false;
            }
        },
        showSearch() {
            return (this.dialogSearchVisible = true);
        },
        resetSearchQuery() {
            this.searchQuery = "";
            this.routeHandler();
        },
        changePage(pageNumber) {
            if (pageNumber < 1 || pageNumber > this.totalPages) return;
            this.page = pageNumber;
            this.routeHandler();
            this.getItems();
        },
        routeHandler() {
            this.$router.push({
                path: this.routeName,
                query: { search: this.searchQuery, page: Number(this.page) },
            });
        },
    },

    computed: {
        routeName() {
            return this.$route.name as string;
        },
        itemClass() {
            return this.routeName;
        },
        routeQuery() {
            return this.$route.query;
        },
    },

    watch: {
        routeName() {
            this.getItems();
            this.isLoading = true;
            this.searchQuery = "";
            this.page = 1;
        },
        searchQuery() {
            if (this.searchQuery) {
                this.page = 1;
                this.$router.push({
                    path: this.routeName,
                    query: { search: this.searchQuery, page: this.page },
                });
                this.getItems();
            }
        },
        routeQuery: {
            deep: true,
            handler() {
                this.searchQuery = (this.routeQuery.search as string) || "";
                this.page = Number(this.routeQuery.page) || 1;
                this.$nextTick(() => {
                    this.$router.push({
                        path: this.routeName,
                        query: { search: this.searchQuery, page: this.page },
                    });
                    this.getItems();
                });
            },
        },
    },
});
